import { Component, ElementRef, EventEmitter, Input, OnChanges, OnDestroy, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import * as Chart from 'chart.js';

@Component({
  selector: 'lib-base-chart',
  templateUrl: './base-chart.component.html'
})
export class BaseChartComponent implements OnInit, OnDestroy, OnChanges {
  
  @ViewChild('chartCanvas') canvasRef: ElementRef;

  @Input() type: Chart.ChartType;
  @Input() data: Chart.ChartData;
  @Input() options: Chart.ChartOptions;
  @Input() plugins: any;

  @Output() public chartClick: EventEmitter<{ event?: MouseEvent, active?: {}[] }> = new EventEmitter();
  @Output() public chartHover: EventEmitter<{ event: MouseEvent, active: {}[] }> = new EventEmitter();

  private _options: Chart.ChartOptions;
  private _chart: Chart;

  constructor() {
  }

  ngOnInit() {

    if (this.options) {

      const opt = Object.assign(Object.create(Object.getPrototypeOf(this.options)), this.options);

      opt.onClick = (event?: MouseEvent, active?: {}[]) => {        
        this.chartClicked(event, active);        
      };

      opt.hover = opt.hover || {};
      opt.hover.onHover = (event: MouseEvent, active: {}[]) => {
        this.chartHovered(event, active);      
      };      
      this._options = opt;

    } else {
      
      const opt: Chart.ChartOptions = {};
      opt.onClick = (event?: MouseEvent, active?: {}[]) => {        
        this.chartClicked(event, active);
      };

      opt.hover = opt.hover || {};
      opt.hover.onHover = (event: MouseEvent, active: {}[]) => {
        this.chartHovered(event, active);        
      };

      this._options = opt;
    }

    this._chart = new Chart(this.canvasRef.nativeElement, {
      type: this.type,
      data: this.data,
      options: this._options,
      plugins: this.plugins      
    });
  }

  private chartClicked(event: MouseEvent, chartObj: any) {
    this.chartClick.emit(chartObj);
  }

  private chartHovered(event: MouseEvent, chartObj: any) {
    this.chartHover.emit(chartObj);
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  ngOnDestroy(): void {
    if (this._chart) {
      this._chart.destroy();
    }
  }
}
