import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import * as waterfallPlugin from 'chartjs-plugin-waterfall';

@Component({
  selector: 'lib-waterfall-plugin-chart',
  templateUrl: './waterfall-plugin-chart.component.html',
  styleUrls: ['./waterfall-plugin-chart.component.scss']
})
export class WaterfallPluginChartComponent implements OnInit, OnChanges {

  @Input() waterfallPluginChartData: WaterfallPluginChartData[];

  chartData: Chart.ChartData;
  plugins = [waterfallPlugin];

  @Output() chartClick: EventEmitter<any> = new EventEmitter();
  @Output() chartHover: EventEmitter<any> = new EventEmitter();
  
  constructor() {
  }

  ngOnInit() {
    this.adaptDataForPlugin();
  }

  ngOnChanges(changes: SimpleChanges): void {
  }

  private adaptDataForPlugin() {
    if (!this.waterfallPluginChartData) {
      return;
    }

    const adaptedData = [];
    let currentTotal = 0;

    this.waterfallPluginChartData.forEach((v, idx) => {

      if (!!v.total) {
        currentTotal = v.data;
      } else {
        if (v.data < 0) {
          currentTotal += v.data;
        }

        adaptedData.push({
          data: [currentTotal],
          waterfall: {
            dummyStack: true
          },
          stack: `${idx}`
        });

        if (v.data > 0) {
          currentTotal += v.data;
        }
      }

      if (v.backgroundColor) {
        adaptedData.push({
          label: v.label,
          data: [Math.abs(v.data)],
          backgroundColor: v.backgroundColor,
          stack: `${idx}`
        });
      } else {
        let backgroundColor: string[] = [];
        if (v.total) {
          backgroundColor = ['rgba(255, 99, 132, 0.5)'];
        } else {
          if (v.data > 0) {
            backgroundColor = ['rgba(54, 162, 235, 0.5)'];
          } else {
            backgroundColor = ['rgba(255, 206, 86, 0.5)'];
          }
        }
        adaptedData.push({
          label: v.label,
          data: [Math.abs(v.data)],
          backgroundColor: backgroundColor,
          stack: `${idx}`
        });
      }
    });

    this.chartData = {
      labels: ['Waterfall Model'],
      datasets: adaptedData
    };
  }

  options = {    
    plugins: {
      datalabels: {
        display: false,
      },
      waterFallPlugin: {
        stepLines: {
          enabled: false,          
        },
      },
    },    
  }

  public chartClicked(event: any) {
 
    this.chartClick.emit(event);
  }

  public chartHovered(event: any) {
  
    this.chartHover.emit(event);
  }
}

export interface WaterfallPluginChartData {
  label: string;
  data: number;
  backgroundColor?: string;
  // marks data as total
  total?: boolean;
}