import { ChartOptions } from 'chart.js';
import { GlobalChartColors } from './global-chart-colors';

export class GlobalChartOptions {

  public static FONT_FAMILY = 'Helvetica Neue';
  public static FONT_SIZE = 12;

  public peityPieOptions(width: number, height: number): any {
    let options: any = {
      delimiter: null,
      fill: GlobalChartColors.globalBackgroundColors,
      height: height,
      radius: 8,
      width: width
    }
    return options;
  }

  public peityDonutOptions(width: number, height: number): any {
    let options: any = {
      delimiter: null,
      fill: GlobalChartColors.globalBackgroundColors,
      height: height,
      innerRadius: null,
      radius: 8,
      width: width
    }
    return options;
  }

  public peityLineOptions(width: number, height: number): any {
    let options: any = {
      delimiter: ",",
      fill: GlobalChartColors.globalBackgroundColors[0],
      max: null,
      min: 0,
      stroke: GlobalChartColors.globalBackgroundColors[1],
      strokeWidth: 1,
      height: height,
      width: width
    }
    return options;
  }

  public peityBarOptions(width: number, height: number): any {
    let options: any = {
      delimiter: ",",
      fill: [GlobalChartColors.globalBackgroundColors[0]],
      max: null,
      min: 0,
      padding: 0.1,
      height: height,
      width: width
    }
    return options;
  }

  public barChartOptions: ChartOptions = {
    responsive: true,
    scales: {
      xAxes: [{
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }],
      yAxes: [{

        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }]
    },
    plugins: {
      datalabels: {
        anchor: 'end',
        align: 'end',
      }
    }
  };

  public scatterChartOptions: ChartOptions = {
    responsive: true,
    scales: {
      xAxes: [{
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }],
      yAxes: [{
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }]
    },
    plugins: {
      datalabels: {
        display: false,
      }
    },
  };

  public pieChartOptions: ChartOptions = {
    responsive: true,
    plugins: {
      datalabels: {
        formatter: (value, ctx) => {
          const label = ctx.chart.data.labels[ctx.dataIndex];
          return label;
        },
      },
    }
  };

  public waterfallChartOptions: any = {

    xaxis: {
      type: 'category',
      automargin: true
    },
    yaxis: {
      type: 'linear',
      automargin: true
    },
    margin: {
      t: 60,
    },
    font: {
      family: GlobalChartOptions.FONT_FAMILY,
      size: GlobalChartOptions.FONT_SIZE,
      color: GlobalChartColors.FONT_COLOR
    },
    autosize: false,
    width: 1150,
    height: 800,
    showlegend: false,
    paper_bgcolor: 'rgba(0,0,0,0)',
    plot_bgcolor: 'rgba(0,0,0,0)'
  };

  public boxPlotChartOptions: any = {

    xaxis: {
      type: 'category',
      showgrid: false,
      automargin: true
    },
    yaxis: {
      type: 'linear',
      showgrid: true,
      automargin: true,
      gridcolor: GlobalChartColors.GRID_COLOR,
      gridwidth: 1
    },
    font: {
      family: GlobalChartOptions.FONT_FAMILY,
      size: GlobalChartOptions.FONT_SIZE,
      color: GlobalChartColors.FONT_COLOR
    },
    autosize: true,
    showlegend: false,
    paper_bgcolor: 'rgba(0,0,0,0)',
    plot_bgcolor: 'rgba(0,0,0,0)'
  };

  public lineChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,
    plugins: {
      datalabels: {
        display: true,
        anchor: 'end',
        align: 'end',
      }
    },
    scales: {
      xAxes: [{

        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }

      }],
      yAxes: [{
        id: 'y-axis-0',
        position: 'left',
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      },
      {
        id: 'y-axis-1',
        position: 'right',
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }
      ]
    },
    annotation: {
    },
  };

  public combinedChartOptions: (ChartOptions & { annotation: any }) = {
    responsive: true,
    plugins: {
      datalabels: {
        display: false,
      }
    },
    scales: {
      xAxes: [{

        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }],
      yAxes: [
        {
          id: 'y-axis-0',
          position: 'left',
          gridLines: {
            color: GlobalChartColors.GRID_COLOR
          },
          ticks: {
            fontFamily: GlobalChartOptions.FONT_FAMILY,
            fontSize: GlobalChartOptions.FONT_SIZE,
            fontColor: GlobalChartColors.FONT_COLOR,
          }
        }
      ]
    },
    annotation: {
    },
  };

  public variableWidthChartOptions: ChartOptions = {

    responsive: true,
    legend: {
      display: false
    },
    plugins: {
      datalabels: {
        display: false,
      }
    },
    scales: {
      xAxes: [{
        scaleLabel: {
          display: true,
        },
        gridLines: {
          color: GlobalChartColors.GRID_COLOR
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }],
      yAxes: [{
        position: 'left',
        gridLines: {
          display: false
        },
        ticks: {
          fontFamily: GlobalChartOptions.FONT_FAMILY,
          fontSize: GlobalChartOptions.FONT_SIZE,
          fontColor: GlobalChartColors.FONT_COLOR,
        }
      }, {
        position: 'right',
        gridLines: { display: false },
        scaleLabel: { display: false },
        ticks: { display: false }
      }],

    }
  };
}